.img-wrap {
    width: 600px;
    height: 600px;
    position: relative;
    display: inline-block;
    overflow: hidden;
    margin: 0;
    border: 3px solid #ddd;
    border-radius: 20px;
}

div > img {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    transform: translate(-50%, -50%);
}